.MainPage {
    padding-bottom: 0;

    flex-direction: column;
    align-items: center;
}

.MainPage input[type="text"] {
    margin: 25px 0 50px;
    width: 45%;
    height: 50px;
    padding: 0 20px;

    font-size: 25px;
    font-weight: normal;

    border-radius: 50px;
    border: 2px solid rgb(190, 190, 190);
}

.MainPage input[type="text"]:focus {
    outline: none;
    border: 2px solid black;
}