.Card-container {
    width: 30%;
    min-width: 200px;
    height: 70vh;
    padding: 20px 0;
    margin-bottom: 3%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    border: 2px solid black;
}

.Img-border {
    width: 90%;
    padding: 10px;

    border: 2px solid black;
}

.Img-border img {
    width: 100%;
    border: 2px solid black;
    object-fit: contain;
}

.Card-name {
    font-size: 28px;
    text-align: center;
}

.Card-info {
    font-size: 20px;
}