.App {
  min-height: 100vh;
}

.App-header {
  height: 50px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.App-header a {
  height: 100%;
  padding: 2px 2px 0px;

  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;

  background-color: rgb(230, 230, 230);
  border-bottom: 2px solid black;

  color: black;
  font-size: 25px;
}

.App-header a:hover {
  background-color: rgb(190, 190, 190);
}

.App-header a.active {
  padding: 0px 0px 2px;

  background-color: white;
  border: 2px solid black;
  border-bottom: none;
}

.Page-container {
  padding: 25px 0;
  min-height: calc(100vh - 50px);

  display: flex;
  flex-direction: column;
  align-items: center;
}